import React, { Component } from 'react';
import './App.css';
import List from './List';
import {library} from '@fortawesome/fontawesome-svg-core';
import {faTrash} from '@fortawesome/free-solid-svg-icons';


library.add(faTrash);

class App extends Component {
  constructor(props){
    super(props)
    this.state = {
      items: [],
      currentItem: {
        text: '',
        key: ''
      }
    }
    this.inputHandler = this.inputHandler.bind(this);
    this.addButton = this.addButton.bind(this);
    this.deleteItems = this.deleteItems.bind(this);
    this.setUpdate = this.setUpdate.bind(this);

  }

  inputHandler(e){
    this.setState({
      currentItem: {
        text: e.target.value,
        key: Date.now()
      }

    })
  }


  addButton(e){
    e.preventDefault();
    const newItem = this.state.currentItem;

    if (newItem !== ""){
      const newItems = [...this.state.items, newItem];
        this.setState({
          items: newItems,
          currentItem :{
            text: "",
            key:""
          }
        })
    }
  }

deleteItems(key){
  const filteredItems = this.state.items.filter(item =>
    item.key !== key
  );
  this.setState({
    items: filteredItems
  })


}

setUpdate(text, key){
  const items = this.state.items;
  items.map(item => {
    if(item.key === key){
    item.text = text
  }})
  this.setState({
    items: items,
  })
}

  render() {
    return(
      <div className='App'>
        <header>
        <h1 className='title'>To-Do List</h1>
            <form id="to-do-form" onSubmit={this.addButton}>
                <input type="text" placeholder="Enter text..." value={this.state.currentItem.text} onChange={this.inputHandler} />
                <button type="submit">Add</button>


                </form>
          </header>
            <List items={this.state.items} deleteItem={this.deleteItems} setUpdate={this.setUpdate} />

        </div>

    )
  }
}

export default App
